import Vue from "vue";
import Vuex from "vuex";
//import Resoluciones from "@/data/resoluciones_dummy";
import resolucionesLocal from "@/data/resoluciones";
import { getResoluciones } from "../api/resoluciones";

Vue.use(Vuex);

function filtrarCeros(tipoResoluciones) {
  const aux = {};
  Object.keys(tipoResoluciones).forEach((key) => {
    if (tipoResoluciones[key] !== 0) aux[key] = tipoResoluciones[key];
  });

  return aux;
}

export default new Vuex.Store({
  state: {
    datosDesactualizados: false,
    year: null,
    title: "",
    resoluciones: [],
    resolucionActual: null,
    links: {
      repositorioDatos:
        //"https://drive.google.com/drive/folders/1Wmk1dWN31bymjBryk3bwzBCQvZa7gwI3",
        "https://docs.google.com/spreadsheets/d/1uAi-Yfq-rJl_cqQaVe9Fv1rLlBJcmEtDpUB0NTOrLAs",
      // agenda:
      //"https://calendar.google.com/calendar/embed?height=600&amp;wkst=1&amp;bgcolor=%23ffffff&amp;ctz=America%2FArgentina%2FBuenos_Aires&amp;src=anV6Z2FkbzEwcGN5ZkBnbWFpbC5jb20&amp;src=ZXMtNDE5LmFyI2hvbGlkYXlAZ3JvdXAudi5jYWxlbmRhci5nb29nbGUuY29t&amp;color=%23039BE5&amp;color=%230B8043&amp;showTz=0&amp;showCalendars=0&amp;showPrint=0&amp;showTitle=0",
      //"https://calendar.google.com/calendar/embed?height=600&amp;wkst=1&amp;bgcolor=%23ffffff&amp;ctz=America%2FArgentina%2FBuenos_Aires&amp;src=anV6Z2FkbzEwcGN5ZkBnbWFpbC5jb20&amp;src=ZXMtNDE5LmFyI2hvbGlkYXlAZ3JvdXAudi5jYWxlbmRhci5nb29nbGUuY29t&amp;color=%23039BE5&amp;color=%230B8043&amp;showCalendars=0&amp;showPrint=0&amp;showTitle=0&amp;showNav=1",
      documentoLenguaje:
        "https://drive.google.com/file/d/17lZJbD3F9ArHkZUHfDhFZqWw9SQO2cph/preview",
      manualProcedimientos:
        "https://drive.google.com/file/d/0B-HNNg3Su-HFYW9NR1RVZjhkRm1xTEs2VmMxbkFiUERVOTM4/preview",
      documentoPlanificacion:
        "https://drive.google.com/file/d/1ewcsYz1CEe0QxaM3LEK0wA7uK0hjcZUQ/preview",
      ubicacion:
        "https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d13135.13808738704!2d-58.3795567!3d-34.6096099!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x95bccad10638ec91%3A0xcde18020281e0a94!2sFiscal%C3%ADa%20de%20la%20Ciudad%20-%20Fiscal%C3%ADa%20de%20C%C3%A1mara%20CAyT!5e0!3m2!1sen!2sar!4v1584217906852!5m2!1sen!2sar",
      twitter: "https://twitter.com/jpcyf10?ref_src=twsrc%5Etfw",
    },
  },
  mutations: {
    SET_YEAR(state, year) {
      state.year = year;
      state.resolucionActual = state.resoluciones.find(
        (x) => x.year == state.year
      );
    },
    SET_TITLE(state, title) {
      state.title = title;
    },
    SET_RESOLUCIONES(state, resoluciones) {
      state.resoluciones = resoluciones;
    },
    SET_DATOS_DESACTUALIZADOS(state, desactualizado) {
      state.datosDesactualizados = desactualizado;
    },
  },
  actions: {
    getResoluciones({ commit, state }) {
      return getResoluciones()
        .then((data) => {
          commit("SET_RESOLUCIONES", data.resoluciones);
          commit("SET_YEAR", state.year);
          commit("SET_DATOS_DESACTUALIZADOS", false);
        })
        .catch((error) => {
          // Si algo falla, carga los datos locales.
          console.error(error);

          commit("SET_RESOLUCIONES", resolucionesLocal);
          commit("SET_YEAR", state.year);
          commit("SET_DATOS_DESACTUALIZADOS", true);
        });
    },
  },
  getters: {
    getResolucionesMes: (state) => (semestre) => {
      if (!state.resolucionActual) return null;

      const cantMes = state.resolucionActual.resolucionesPorMes.map(
        (x) => x.cant
      );

      const resolucionesSemestre =
        semestre === 1 ? cantMes.slice(0, 6) : cantMes.slice(6);

      return resolucionesSemestre;
    },
    getTiposLabel: (state) => {
      if (!state.resolucionActual) return null;

      const tipoResolucionConValor = filtrarCeros(
        state.resolucionActual.tipoResoluciones
      );
      const keys = Object.keys(tipoResolucionConValor);
      return keys;
      //return keys.map((x) => `${x} (${tipoResolucionConValor[x]})`);
    },
    getTiposValues: (state) => {
      if (!state.resolucionActual) return null;

      const tipoResolucionConValor = filtrarCeros(
        state.resolucionActual.tipoResoluciones
      );
      return Object.values(tipoResolucionConValor);
    },
    getTotalResoluciones: (state) => {
      if (!state.resolucionActual) return null;
      return state.resolucionActual.totalResoluciones;
    },
    getCasosViolenciaGenero: (state) => {
      if (!state.resolucionActual) return null;
      return state.resolucionActual.casosViolenciaGenero;
    },
    getPorcentajeViolenciaGenero: (state) => {
      if (!state.resolucionActual) return null;

      let porcentaje =
        (state.resolucionActual.casosViolenciaGenero /
          state.resolucionActual.totalResoluciones) *
        100;

      porcentaje = Math.round(porcentaje * 100) / 100;
      return porcentaje;
    },
    getFaltas: (state) => {
      if (!state.resolucionActual) return null;
      if (!("faltas" in state.resolucionActual.tipoResoluciones)) return 0;
      return state.resolucionActual.tipoResoluciones["faltas"];
    },
    getRankingArticulos: (state) => {
      if (!state.resolucionActual) return [];
      return state.resolucionActual.rankingArticulos;
    },
  },
  modules: {},
});
